<template>
  <div>
    <!-- 查询axios时显示的加载进行时 -->
    <van-loading style="margin-top: 100px" size="24px" vertical v-if="loading"
      >加载中...</van-loading
    >
    <!-- 刷新本页 -->
    <van-pull-refresh
      v-model="isLoading"
      success-text="刷新成功"
      @refresh="onRefresh"
      v-else
    >
      <!-- girl主要内容 -->
      <div class="publish">
        <!-- 类型模块一 -->
        <div class="container">
          <Title
            title="热门小说"
            :loading="loading"
            :typeBooks="publishHotNovels"
            >热门小说</Title
          >
          <ul v-if="publishHotNovels">
            <BookItemTypeTwo
              :item="publishHotNovels[randomNumSec]"
              :key="publishHotNovels[randomNumSec]._id"
              :loading="loading"
            />
          </ul>
          <ul class="ul-type-one" v-if="publishHotNovels">
            <BookItemTypeOne
              v-for="item in publishHotNovels.slice(
                0 + randomNum,
                4 + randomNum
              )"
              :item="item"
              :key="item._id"
              :loading="loading"
            />
          </ul>
        </div>

        <!-- 类型模块三 -->
        <div class="container">
          <Title
            title="成功励志"
            :loading="loading"
            :typeBooks="successfulMotivationalNovels"
            >成功励志</Title
          >
          <van-grid
            column-num="3"
            :center="false"
            v-if="successfulMotivationalNovels"
          >
            <BookItemTypeThree
              v-for="item in successfulMotivationalNovels.slice(
                0 + randomNum,
                3 + randomNum
              )"
              :item="item"
              :key="item._id"
              :loading="loading"
            />
          </van-grid>
        </div>

        <!-- 类型模块二 -->
        <div class="container">
          <Title
            title="言情畅销"
            :loading="loading"
            :typeBooks="sentimentalNovel"
            >言情畅销</Title
          >
          <ul v-if="sentimentalNovel">
            <BookItemTypeTwo
              v-for="item in sentimentalNovel.slice(
                0 + randomNum,
                6 + randomNum
              )"
              :item="item"
              :key="item._id"
              :loading="loading"
            />
          </ul>
        </div>

        <!-- 类型模块一 -->
        <div class="container">
          <Title title="时尚生活" :loading="loading" :typeBooks="fashionNovels"
            >时尚生活</Title
          >
          <ul v-if="fashionNovels">
            <BookItemTypeTwo
              :item="fashionNovels[randomNumSec]"
              :key="fashionNovels[randomNumSec]._id"
              :loading="loading"
            />
          </ul>
          <ul class="ul-type-one" v-if="fashionNovels">
            <BookItemTypeOne
              v-for="item in fashionNovels.slice(0 + randomNum, 4 + randomNum)"
              :item="item"
              :key="item._id"
              :loading="loading"
            />
          </ul>
        </div>

        <!-- 回到顶部 -->
        <BackToTop />
      </div>
    </van-pull-refresh>
  </div>
</template>
<script>
import Title from "@/components/Title.vue";
import BookItemTypeOne from "@/components/BookItemTypeOne.vue";
import BookItemTypeTwo from "@/components/BookItemTypeTwo.vue";
import BookItemTypeThree from "@/components/BookItemTypeThree.vue";
import BackToTop from "@/components/BackToTop.vue";
export default {
  components: {
    Title,
    BookItemTypeOne,
    BookItemTypeTwo,
    BookItemTypeThree,
    BackToTop,
  },
  data() {
    return {
      // 小说书籍数据
      publishHotNovels: null,
      sentimentalNovel: null,
      fashionNovels: null,
      successfulMotivationalNovels: null,
      randomNum: 0, //每次书籍展示的起点下标
      randomNumSec: 5, //每次书籍展示的起点下标

      maxSearch: 30, //一次最多搜索到的数量
      isLoading: false,
      loading: true, //等待loading布尔值
      loadingCount: 0,
    };
  },
  watch: {
    loadingCount(n) {
      if (n >= 4) {
        this.loading = false;
      }
    },
  },
  created() {
    this.loading = true;
    this.loadingCount = 0;
    this.searchData();
  },

  mounted() {
    this.getRandomNum();
  },
  methods: {
    // 刷新当前页面的数据
    onRefresh() {
      setTimeout(() => {
        this.isLoading = false;
        this.getRandomNum();
      }, 1000);
    },
    // 获取数据
    searchData() {
      // 出版热门书籍查询
      if (
        localStorage.getItem("publishHotNovels") &&
        JSON.parse(localStorage.getItem("publishHotNovels")).expire > Date.now()
      ) {
        this.publishHotNovels = JSON.parse(
          localStorage.getItem("publishHotNovels")
        ).books;
        // console.log("出版热门书籍已存入本地存储");
        this.loadingCount++;
      } else {
        this.axios
          .get(
            "/category-info?gender=press&type=hot&major=出版小说&start=0&limit=" +
              this.maxSearch
          )
          .then((res) => {
            // console.log("出版热门书籍");
            let obj = {
              books: res.data.books.slice(0, this.maxSearch),
              expire: Date.now() + 24 * 3600 * 1000, //设置时效
            };
            this.publishHotNovels = obj.books;
            localStorage.setItem("publishHotNovels", JSON.stringify(obj));
          })
          .finally(() => {
            this.loadingCount++;
          });
      }

      // 青春言情书籍查询
      if (
        localStorage.getItem("sentimentalNovel") &&
        JSON.parse(localStorage.getItem("sentimentalNovel")).expire > Date.now()
      ) {
        this.sentimentalNovel = JSON.parse(
          localStorage.getItem("sentimentalNovel")
        ).books;
        // console.log("青春言情书籍已存入本地存储");
        this.loadingCount++;
      } else {
        this.axios
          .get(
            "/category-info?gender=press&type=hot&major=青春言情&start=0&limit=" +
              this.maxSearch
          )
          .then((res) => {
            // console.log("青春言情书籍");
            let obj = {
              books: res.data.books.slice(0, this.maxSearch),
              expire: Date.now() + 24 * 3600 * 1000, //设置时效
            };
            this.sentimentalNovel = obj.books;
            localStorage.setItem("sentimentalNovel", JSON.stringify(obj));
          })
          .finally(() => {
            this.loadingCount++;
          });
      }

      // 时尚生活书籍查询
      if (
        localStorage.getItem("fashionNovels") &&
        JSON.parse(localStorage.getItem("fashionNovels")).expire > Date.now()
      ) {
        this.fashionNovels = JSON.parse(
          localStorage.getItem("fashionNovels")
        ).books;
        // console.log("时尚生活书籍已存入本地存储");
        this.loadingCount++;
      } else {
        this.axios
          .get(
            "/category-info?gender=press&type=hot&major=生活时尚&start=0&limit=" +
              this.maxSearch
          )
          .then((res) => {
            // console.log("时尚生活书籍");
            let obj = {
              books: res.data.books.slice(0, this.maxSearch),
              expire: Date.now() + 24 * 3600 * 1000, //设置时效
            };
            this.fashionNovels = obj.books;
            localStorage.setItem("fashionNovels", JSON.stringify(obj));
          })
          .finally(() => {
            this.loadingCount++;
          });
      }

      // 成功励志书籍查询
      if (
        localStorage.getItem("successfulMotivationalNovels") &&
        JSON.parse(localStorage.getItem("successfulMotivationalNovels"))
          .expire > Date.now()
      ) {
        this.successfulMotivationalNovels = JSON.parse(
          localStorage.getItem("successfulMotivationalNovels")
        ).books;
        // console.log("成功励志书籍已存入本地存储");
        this.loadingCount++;
      } else {
        this.axios
          .get(
            "/category-info?gender=press&type=hot&major=成功励志&start=0&limit=" +
              this.maxSearch
          )
          .then((res) => {
            // console.log("成功励志书籍");
            let obj = {
              books: res.data.books.slice(0, this.maxSearch),
              expire: Date.now() + 24 * 3600 * 1000, //设置时效
            };
            this.successfulMotivationalNovels = obj.books;
            localStorage.setItem(
              "successfulMotivationalNovels",
              JSON.stringify(obj)
            );
          })
          .finally(() => {
            this.loadingCount++;
          });
      }
    },
    // 获取随机数
    getRandomNum() {
      // 获取一个随机数
      this.randomNum = Math.floor(Math.random() * (this.maxSearch - 6));
      this.randomNumSec = Math.floor(Math.random() * this.maxSearch);
    },
  },
};
</script>
